import { Button as ButtonComponent, ButtonSize } from 'im.v2.component.elements';
import { SupervisorBaseMessage } from 'im.v2.component.message.supervisor.base';

import { tools } from './const/tools';
import './css/enable-feature.css';

import type { ImModelMessage } from 'im.v2.model';
import type { CustomColorScheme } from 'im.v2.component.elements';

const TOOL_ID_PARAMS_KEY = 'toolId';
const BUTTON_COLOR = '#52c1e7';

// @vue/component
export const SupervisorEnableFeatureMessage = {
	name: 'SupervisorEnableFeatureMessage',
	components: { ButtonComponent, SupervisorBaseMessage },
	props: {
		item: {
			type: Object,
			required: true,
		},
		dialogId: {
			type: String,
			required: true,
		},
	},
	data(): {showAddToChatPopup: boolean}
	{
		return {
			showAddToChatPopup: false,
		};
	},
	computed:
	{
		message(): ImModelMessage
		{
			return this.item;
		},
		ButtonSize: () => ButtonSize,
		buttonColorScheme(): CustomColorScheme
		{
			return {
				backgroundColor: 'transparent',
				borderColor: BUTTON_COLOR,
				iconColor: BUTTON_COLOR,
				textColor: BUTTON_COLOR,
				hoverColor: 'transparent',
			};
		},
		toolId(): string
		{
			return this.message.componentParams[TOOL_ID_PARAMS_KEY];
		},
		tool(): { title: string, description: string }
		{
			return tools[this.toolId];
		},
		modifierImageClasses(): string
		{
			return `--${this.toolId}`;
		},
	},
	methods:
	{
		loc(phraseCode: string, replacements: {[p: string]: string} = {}): string
		{
			return this.$Bitrix.Loc.getMessage(phraseCode, replacements);
		},
	},
	template: `
		<SupervisorBaseMessage
			:item="item"
			:dialogId="dialogId"
			:title="loc(tool.title)"
			:description="loc(tool.description)"
		>
			<template #image>
				<div :class="['bx-im-message-enable-feature__image', modifierImageClasses]" />
			</template>
			<template #actions>
				<ButtonComponent
					:size="ButtonSize.L"
					:isRounded="true"
					:text="loc('IM_MESSAGE_SUPERVISOR_ENABLE_FEATURE_BUTTON_OPEN_SETTINGS')"
				/>
				<ButtonComponent
					:size="ButtonSize.L"
					:customColorScheme="buttonColorScheme"
					:isRounded="true"
					:text="loc('IM_MESSAGE_SUPERVISOR_ENABLE_FEATURE_BUTTON_MORE_DETAILED')"
				/>
			</template>
		</SupervisorBaseMessage>
	`,
};
